/* 详情页样式 */
.detail-page {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
}

.detail-header {
  padding: 40px 60px;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  gap: 30px;
}

.back-btn2 {
  position: absolute;
  top: 1600px;
  left: 760px;
  width: 240px;
  height: 100px;
}

.detail-header h1 {
  font-size: 48px;
  color: white;
  margin: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.detail-header p {
  font-size: 24px;
  color: rgba(255, 255, 255, 0.9);
  margin: 0;
}

.detail-content {
  flex: 1;
  padding: 60px;
  color: white;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.detail-content p {
  font-size: 24px;
  line-height: 1.8;
}

