.back-button {
  position: absolute;
  right: 22px;
  bottom: 0;
  z-index: 10;
  
  transition: transform 0.2s ease;
}

.back-button:hover {
  transform: scale(1.05);
}

.back-button:active {
  transform: scale(0.95);
}

.back-button img {
  width: auto;
  height: auto;
  display: block;
}

.detail-text-content {
  position: absolute;
  top: 588px;
