  right: 0;
  bottom: 0;
  width: 845px;
  height: 965px;
  padding: 60px 80px;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  z-index: 1;
}

.detail-text-content p {
  font-family: 'FZLanTingHeiS-DB-GB', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 48px;
  text-align: justify;
  color: #2D2A28;
  text-indent: 2em;
  white-space: pre-wrap;
  word-wrap: break-word;
  margin: 0;
}